@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

}
body {
  background-color: #b5fac7;
}
body,
button {
  font-family: "Poppins", sans-serif;
}
/* header */
header {
  display: flex;
  width: 90%;
  position: relative;
  top: 30px;
  margin: auto;
  align-items: center;
}
.logo-container,
.nav-links,
.cart {
  display: flex;
}
.logo-container {
  flex: 1;
  align-items: center;
}
.logo {
  font-weight: 400;
  margin: 5px;
}
nav {
  flex: 2;
}
.nav-links {
  justify-content: space-around;
  list-style: none;
}
.nav-link {
  color: #5f5f79;
  font-size: 1.125rem;
  text-decoration: none;
}
.nav-link:hover {
  transform: scale(1.3);
  color: red;
  text-decoration: underline;
}
.cart {
  flex: 1;
  justify-content: flex-end;
  cursor: pointer;
}
/* hero */
.presentation {
  display: flex;
  position: relative;
  top: 50px;
  width: 90%;
  margin: auto;
  min-height: 76vh;
  align-items: center;
}
.introduction {
  flex: 1;
}
.intro-text h1 {
  font-size: 2.75rem;
  font-weight: 500;
  background-color: #494964;
  background: linear-gradient(to right, #494964, #6f6f89);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.intro-text p {
  margin-top: 5px;
  font-size: 1.375rem;
  color: #585772;
}
.cta {
  padding: 50px 0 0 0;
}
.cta-select {
  border: 2px solid #c36cbb;
  background: transparent;
  color: #c36cbb;
  width: 150px;
  height: 50px;
  cursor: pointer;
  font-size: 1rem;
}
.cta-select:hover {
  background-color: aqua;
  transform: scale(1.1);
  transition: 0.5s;
}
.cta-add {
  background: #c36cbb;
  width: 150px;
  height: 50px;
  cursor: pointer;
  font-size: 1rem;
  border: none;
  color: #fff;
  margin: 30px 0px 0px 30px;
}
.cta-add:hover {
  background-color: aqua;
  transition: 0.5s;
  transform: scale(1.1);
}
.cover {
  flex: 1;
  display: flex;
  justify-content: center;
  height: 60vh;
}
.cover img {
  height: 70%;
  filter: drop-shadow(5px 5px 10px black);
  animation: drop 1.5s ease;
}
@keyframes drop {
  0% {
    opacity: 0;
    transform: translateY(-80px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.laptop-select {
  width: 15%;
  display: flex;
  justify-content: space-around;
  position: absolute;
  right: 20%;
}
.laptop-select img {
    bottom: 10px;
    position: relative;
    width: 150px;
  }
  .laptop-select img:hover {
    transition: 0.3s;
    transform: scale(1.3);
    cursor: pointer;
  }
@media (max-width: 768px) {
  .logo-container {
    flex: 3;
  }
  .nav-links {
    display: none;
  }
  .cover img {
    max-width: 100%;
    height: auto;
  }
  .laptop-select img {
    position: relative;
    top: 50px;
    width: 60px;
    height: 70px;

  }
}
@media (max-width:400px) {
  .logo-container {
    flex: 3;
  }
  .nav-links {
    display: none;
  }
  .cover img {
    max-width: 100%;
    height: auto;
  }
  .laptop-select img {
    position: relative;
    top: 150px;
    width: 60px;
    height: 70px;
  }
}
@media (max-width: 1024px) {
  .presentation {
    flex-direction: column;
  }
  .introduction {
    margin-top: 5vh;
    text-align: center;
  }
  .intro-text h1 {
    font-size: 1.875rem;
  }
  .intro-text p {
    font-size: 1.125rem;
  }
  .cta {
    padding: 10px 0 0 0;
  }
  .laptop-select {
    bottom: 5%;
    right: 50%;
    width: 50%;
    transform: translate(50%, 5%);
  }
  .cover img {
    height: 80%;
  }
}
 